import React from 'react';
import './Hint.css';

class Hint extends React.Component {
  constructor(props) {
    super(props);
    this.state = {date: new Date()};
  }

  componentDidMount() {
    this.timerID = setInterval(
      () => this.tick(), 60000
    );
  }

  componentWillUnmount() {
    clearInterval(this.timerID);
  }

  hint1() {
    return (React.createElement("p", {
      className: "Hint"
    }, atob("SGludDogQ2lwaGVy")));
  }

  nothing() {
    return React.createElement("p", null);;
  }

  render() {
    const check = this.state.date < new Date(2021, 3, 1) ? true : false;
    if (check) {
      return this.hint1();
    }
    return this.nothing();
  }

  tick() {
    this.setState({
      date: new Date()
    });
  }
}

export default Hint;
